.addtodo-form{
    padding: 10px;
    margin:8px;
    
    display: flex;
    flex-direction: row;
    
     
background: #191B20;
opacity: 0.8;
border-radius: 16px;
}
.addtodo-money-bag>img{
    position: relative;
    display: inline-block;
    left:7px;
    width:8.75px;
    height: 10.77px;
    
}
.addtodo-money-bag{

    width: 24px;
    height: 24px;
    border-radius: 24px;
    background : linear-gradient(135deg, #BBB5E2 0%, #9C92DF 100%);
}
.input-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap:10px;
}
.addtodo-top{
    display: flex;
}
.addtodo-top input{
    width: 150px;
height: 24px;
flex-grow: none;
/* font-family: 'Poppins'; */
font-style: normal;
font-weight: 700;
font-size: 18px;
line-height: 24px;
/* identical to box height, or 133% */


color: #6E6E6E;
border:none;

/* Inside auto layout */
background-color: #191B20;
outline:none;
box-shadow: none;
}
.input-container textarea{
    width: 210px;
height: 46px;

/* font-family: 'Inter'; */
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 16px;
/* or 100% */


/* text colour */

color: #808191;
border:none;
box-shadow: none;
outline:none;
resize: none;
background-color: #191B20;
}
.additem_btn{
    
    cursor: pointer;
    width: 25px;
height: 25px;
border:none;
background: #353945;
border-radius: 12px;
}

.plus_add{
    
    position: relative;
    font-size: xx-large;
    font-weight: bold;
    width: 15px;
    height: 15px;
    color:white;
    bottom: 7px;
    right: 3px;
}
